en todos los portafolios de productos y servicios donde lo puede adquirir, marcas, precios, canales de distribución. El cliente comunicó mediante un mensaje lo que quería y el receptor recibe la información y le ofrece lo que quiere para satisfacer su necesidad.")]),_c('p',{staticClass:"mb-0"},[_vm._v("Es muy importante abordar el canal de comunicación por medio del cual interactúa con el cliente; las estrategias son distintas para cada canal, y más hablando de la era digital actual.")])])]),_c('div',{staticClass:"row justify-content-center",attrs:{"titulo":"Al interactuar"}},[_c('div',{staticClass:"col-lg-11"},[_c('p',[_vm._v("Con el cliente, debe tener en cuenta la comunicación verbal y la comunicación no verbal. En cuanto a la comunicación verbal, debe ser claro, usar un lenguaje adecuado, una tonalidad de voz apropiada, con una buena técnica de vocalización, que logre ser entendido; también es muy recomendable usar técnicas que le permitan abordar el miedo a expresarse, que le permitan controlar los nervios.")]),_c('p',{staticClass:"mb-0"},[_vm._v("La comunicación no verbal es muy importante dentro del proceso de comunicación, por lo cual, debe tener una correcta postura de su cuerpo, un rostro amable, amigable con el cliente y que genere seguridad, debe mirar siempre a la persona con la cual interactúa, esto transmite seguridad, se debe tener cuidado con la posición de las manos, se debe tener una distancia adecuada; por último y no menos importante, se debe llevar un vestuario adecuado.")])])])])],1)])])],1)}
